* {
  box-sizing: border-box;
}
button {
  outline: none;
  border: 0;
  cursor: pointer;
}

header {
  background-color: blanchedalmond;
  padding : 1em;
  font-size : 2rem;
}

.add-form {
  margin : 1em ;
}

.habit-input {
  font-size: 2rem;
}

.add-button {
  background-color: green;
  margin : 0.5em;
  font-size: 1.5rem;
  color: white;
  padding : 0.3em;
  cursor: pointer;
}

.fa-leaf{
  color : green;
  margin-right: 0.5em;
}

.active-count{
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2em;;
  background-color: green;
  color: white;
  border-radius: 50%;
}

.habit {
  font-size: 2.5rem;
  padding: 0.5em;
  list-style: none;
}

.habit-count {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2em;
  margin-right: 0.5em;
  background-color: darkseagreen;
  color: white;
  border-radius: 50%;
}

.habit-button {
  font-size: 2.5rem;
  margin: 0 0.1em;
  background-color: transparent;
}

.habit-button:hover{
  opacity: 0.8;
}

.habit-increase,
.habit-decrease{
  color : goldenrod;
}

.habit-delete{
  font-size:2rem;
  color : darkred;
}

.reset-button{
  font-size: 1.5rem;
  background-color: green;
  color : white;
  margin : 1em;
  padding : 0 0.2em;
}